import React from 'react'

class StatusUpdate extends React.Component{

    state = {
        status: 'Make a Plan '
    };
    componentDidUpdate =()=> {
      var statusID= document.getElementById("statusID")
      if (this.state.status === "Plan Made"){statusID.style.color="green"
      }
      else{
        statusID.style.color="blue"
      }
       
    }
  updateStatus = ()=> {
      this.setState({status: 'Plan Made'});
    };
    
    render() {
        console.log('will this work');
        return(
            <div>
                
                <h1> Getting Things Done âœ…</h1>
                <p id="statusID">Status: {this.state.status} </p>
                <button onClick={this.updateStatus}> change status</button>
            </div>
        );
    }
}


export default StatusUpdate
